body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    box-sizing: border-box;
}

.start{
    background-image: url('https://images.pexels.com/photos/3965551/pexels-photo-3965551.jpeg?auto=compress&cs=tinysrgb&h=650&w=940');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100%;

}

.first{
    background-color: #ff575749;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}


.start .logo{
    margin: 150px 0px 20px 0px;
    border-radius: 50%;
    width: 212px;
    height: 212px;
    left: calc(50% - 212px/2);
    bottom: 300px;
}

.start h1{
    color: white;
    font-family: 'Lato', sans-serif;
    font-size: 40px;
    margin: 10px;
}

.start h2{
    font-family: 'Lato', sans-serif;
    color: white;
    font-size: 30px;
    margin: 10px;
    font-weight: 300;
}

.start button{
    margin: 20px 20px;
    padding: 10px;
    border-radius: 20px;
    border: none;
    width: 100px;
    height: 50px;
    background-color: white;
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    
    
}

.start button:hover{
    background-color: #FF5757;
    color: white;
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.582), 0 17px 50px 0 rgba(0, 0, 0, 0.555);
}


