    .start .logo{
        margin: 110px 0px 20px 0px;
    }
}

@media (height: 667px) {
    .start .logo{
        margin: 160px 0px 20px 0px;
    }
}

@media (height: 736px) {
    .start .logo{
        margin: 180px 0px 20px 0px;
    }
}

@media (height: 812px) {
    .start .logo{
        margin: 210px 0px 20px 0px;
    }
}

@media (height: 823px) {
    .start .logo{
        margin: 230px 0px 20px 0px;
    }
}

@media (height: 1024px) {
    .start .logo{
        margin: 250px 0px 20px 0px;
        width: 300px;
        height: 300px;
    }
    .start h1{
        font-size: 60px;
        
    }
    
    .start h2{
