*{
    box-sizing: border-box;
}

.container-one{
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-gap: 10px;
    grid-auto-rows: 50px;
    grid-template-areas:
      "a a a b b b b b b"
      "a a a b b b b b b"
      "a a a c c c c d d"
      "a a a c c c c d d";
    width: 95%;
    margin: 10px;

    border-radius: 20px;
    background-color: rgb(255, 255, 255);
    box-shadow: -1px 5px 7px 3px rgba(0, 0, 0, 0.267);
    
}

.container-one .colum_one{
    align-self: center;
    text-align: center;
    grid-area: a;

    font-family: 'Lato', sans-serif;
    margin-left: 15px;
}

.colum_one .circle{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 85px;
    height: 85px;
    border-radius: 50%;
}

.container-one .colum_two{
    align-self: center;
    justify-self: center;
    text-align: center;
    grid-area: b;

    font-family: 'Lato', sans-serif;
}

.container-one .colum_four{
    align-self: flex-end;
    margin-bottom: 30px;
    grid-area: d;
    

    font-family: 'Lato', sans-serif;
}

.colum_four button{
    border-radius: 20px;
    border: none;
    width: 80px;
    height: 50px;
    background-color:  #FF5757;
    color: white;
    font-family: 'Lato', sans-serif;
    font-weight: 500;

    margin-left: -55px;
}

.colum_four button:hover{
    background-color: seagreen;
    color: white;
    font-family: 'Lato', sans-serif;
    box-shadow: 3px 4px 9px 0 rgba(0, 0, 0, 0.582);
   
}


/*responsive*/

@media (width: 280px) {
    .container-one{
        grid-template-areas:
          "a a a b b b b b b"
          "a a a b b b b b b"
          "a a a e c c c c c"
          "a a a e c c c c c"
          "a a a e d d d d d";
    }


    .container-one .colum_one{
        font-family: 'Lato', sans-serif;
        margin-left: 20px;
        font-size: 20px;
    }
