header{
    display: grid;
    grid-template-columns: 60% 40%;
    grid-template-rows: 1fr;

    width: 100%;
    height: 25%;
    left: 0px;
    top: 0px;

    background: linear-gradient(0deg, #FFFFFF, #FFFFFF), linear-gradient(180deg, rgba(0, 0, 0, 0.25) 0%, #FFFFFF 100%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 28px;
}

header .logo{
    width: auto;
    height: 70px;
    margin-left: 60px;
    margin-bottom: 25px;
}

header .networks {
    display: grid;
    grid-template-columns: 32% 32% 32%;
    
    height: auto;
    width: 85%;
}
header a{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    margin-top: 10px;
    margin-right: 5px;
    width: 50px;
    height: 50px;

    font-size: 25px;
    color: rgb(0, 0, 0);
}

.fa-facebook {
    color: #3B5998;
}

.fa-whatsapp {
    color: seagreen;
}

.fa-instagram{
    color: #cc2366;
}

.fab:hover{
    opacity: 0.5;
}



.Categories{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 40% 60%;

    height: 100px;
}
.Categories h2{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-left: 20px;
    margin-top: 25px;
    font-family: 'Lato', sans-serif;
    
    font-style: normal;
    font-weight: 500;
    font-size: 25px;
    line-height: 41px;

    color: #FF5757;
}

.Categories .container{
    background-color: rgb(255, 255, 255);


    position: absolute;
    width: 100%;
    height: 70px;
    left: 0px;
    top: 100px;

    overflow-x: scroll;
}
ul{
    list-style-type: none;
    margin-top: 10px;
}
.Categories .container li{
    display: inline;
    text-align: center;
    top: 135px;
    margin: 0 10px 0 0;
    overflow-x: scroll;
}



.container button{
    margin: 8px;
    padding: 10px;
    width: 100px;
    border: none;
    color: #FF5757;
    background-color: white;
    box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.267);
    border-radius: 50px;
}

.container button:hover{
    color: rgb(255, 255, 255);
    background-color:#FF5757;
}

.menu{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr ;
    height: 390px;
    width: 90%;

    overflow-y: scroll;
    margin: 0px 10px 10px 10px;
    margin-left: 10px;
}

.container_total{
    z-index: 1;
    display: grid;
    grid-template-columns: 25% 25% 50%;
    grid-template-rows: 1fr;
    height: auto;
    width: 100%;

    background: white;
    /* border-radius: 28px; */
    box-shadow: 0 -9px 20px 0px rgba(0, 0, 0, 0.25);
}

.container_total h2{
    margin-left: 20px;
}
.container_total h3{
    margin-left: 15px;
    margin-top: 25px;
}

.container_total .Order{
    margin: 10px 20px;
    margin-left: 60px;
    border-radius: 20px;
    border: none;
    width: 80px;
    height: 50px;
    background-color: #FF5757;
    color: #FFFFFF;
    font-family: 'Lato', sans-serif;
    font-weight: 300;
}

.container_total i{
    margin-left: 2px;
}

.container_total .Order:hover{
    background-color: seagreen;
    color: white;
    font-family: 'Lato', sans-serif;
    box-shadow: 3px 4px 9px 0 rgba(0, 0, 0, 0.582);
   
}

.container_total .btn_total{
    display: none;
}



