*{
    box-sizing: border-box;
}

.header{
    display: grid;
    grid-template-columns: 60% 40%;
    grid-template-rows: 1fr;

    /* position: absolute; */
    width: 100%;
    height: 25%;
    left: 0px;
    top: 0px;

    background: linear-gradient(0deg, #FFFFFF, #FFFFFF), linear-gradient(180deg, rgba(0, 0, 0, 0.25) 0%, #FFFFFF 100%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 28px;
}

.header .logo{
    width: auto;
    height: 50px;
    left: 50px;
    top: 10px;
}

.header a{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    margin-top: 10px;
    margin-right: 5px;
    margin-left: 10px;
    width: 50px;
    height: 50px;

    font-size: 25px;
    color: rgb(0, 0, 0);
}

.header .behind i{
    width: 20px;
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.header button{
    border-radius: 10px;
    border: none;
    color: #FF5757;
    background-color: white;
    box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.267);

    margin-top: 20px;
    margin-left: 20px;
}

.header button:hover{
    color: rgb(255, 255, 255);
    background-color: #FF5757;
}

.categories_two .P{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.categories_two h4{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    
}

.menu_two{
    display: grid;
    grid-template-columns: 1fr;
    height: 420px;
    width: 90%;

    overflow-y: scroll;
    margin: 0px 10px 10px 10px;
    margin-left: 10px;
}

.container_total_two{
    z-index: 1;
    display: grid;
    grid-template-columns: 25% 25% 50%;
    grid-template-rows: 1fr;
    height: auto;
    width: 100%;

    background: white;
    /* border-radius: 28px; */
    /* box-shadow: 0 -9px 20px 0px rgba(0, 0, 0, 0.25); */
}

.container_total_two h2{
    margin-left: 20px;
}
.container_total_two h3{
    margin-left: 15px;
    margin-top: 25px;
}

.container_total_two .btn_total{
    margin-top: 10px;
    margin-left: 120px;
    border-radius: 20px;
    border: none;
    width: 80px;
    height: 50px;
    background-color:#FF5757;
    color: #FFFFFF;
    font-family: 'Lato', sans-serif;
    font-weight: 300;
}

.container_total_two i{
    margin-left: 5px;
    color: #FFFFFF;
}

.container_total_two .btn_total:hover{
    background-color: seagreen;
    color: white;
    font-family: 'Lato', sans-serif;
    box-shadow: 3px 4px 9px 0 rgba(0, 0, 0, 0.582);
   
}

